import axios from 'axios'
import Router from '../router'
// import Vue from '@/main'

export function request(config) {
	// 1.创建axios的实例
	const instance = axios.create({
		baseURL: '',
		// baseURL: '/api',
		timeout: 10000
	})

	// 2.axios的拦截器
	// 2.1.请求拦截
	instance.interceptors.request.use(config => {
		let token = window.localStorage.getItem('token');
		if (token != '') {
			config.headers.common['token'] = token;
		}
		return config
	}, err => {
		console.log(err)
	})

	// 2.2.响应拦截
	instance.interceptors.response.use(res => {
		if (res.data.code === 900) {
			Router.push('/login')
		} else {
			if (res.headers.header_token !== undefined && res.headers.header_token !== null && res.headers.header_token !== '') {
				window.localStorage.setItem('token', res.headers.header_token);
			}
			return res.data
		}
		return res.data
	}, err => {
		console.log(err)
	})

	return instance(config)
}
